import liuyunoTabs from "@/components/tablist/navTab.vue";
import uniLoading from '@/components/loading-more/loading.vue';
export default{
	components:{liuyunoTabs,uniLoading},
	data(){
		return{
			pro_text:'',
			tabs: ['待我处理', '我的全部', '我的申请','我已处理'],
			defaultIndex:0, //默认显示列
			status:1, //默认状态
			list:[],//数据列表
			valno:true,
			pageNo: 1,
			index:0, //当前列表下表
			rownumber: 10,
			listindex:['wait','myall','mine','processed'],
			service_name:''
		}
	},
	methods:{
		tabClick(e) {
			this.index=e
			this.list=[]
			this.status=this.pageNo= 1
			this.getlist(true,'pull-down').then(()=>{
				this.loadingStatus(this.list.length)
			})
		},
		colortitle(val){
			switch (val){
				case '完成':
					return '#46B6FE/已完成'
					break;
				case "待业务开发实施":
					return '#09F175/待实施'
					break;
				case "待业务分析":
					return '#e6b011/待分析'
					break;
				case "待业务审核":
					return '#9de65acc/待审核'
					break;
				default:
					return `#e2a06c/${val}`
					break;
			}
		},
		loadingStatus(value) {
			switch (value) {
				case 0:
					if(this.valno){
						this.status = 3
					}else{
						this.status = 2
					}
					break;
				case 10:
					this.status = 0
					break;
				default:
					this.status = 2;
					break;
			}
		},
		//下拉刷新
		// pull-down
		onPullDownRefresh() {
				let _self =this
				_self.pageNo=1
				setTimeout(function() {
					uni.stopPullDownRefresh();
					_self.getlist(true,'pull-down')
				}, 1000);
			},
			// pull-up
		onReachBottom() {
				let _self = this
				_self.status = 1
				_self.pageNo++
				uni.showNavigationBarLoading()
				setTimeout(function() {
					_self.getlist(false,'pull-up')
					uni.hideNavigationBarLoading()
				}, 1000);
			},
		//流程类列表
	async getlist(value,status){
		console.log( this.service_name)
			let url = this.getServiceUrl('oa', this.service_name, 'select');
			let proc_data_type= "wait"
			let req = {
				"serviceName":this.service_name,
				"colNames":["*"],
				"condition":[],
				"page":{pageNo: this.pageNo,
				rownumber: this.rownumber},
				"order":[],
				'proc_data_type':this.listindex[this.index]
			}
			let	response= await this.$http.post(url, req)
				let item =response.data.data
				console.log(item)
				if(status=="pull-down"){
					this.list=item
				}else{
					this.valno=false
					this.loadingStatus(item.length)
					this.list=this.list.concat(item)
				}
				if(value){
					this.valno=true
					this.loadingStatus(item.length)
				}
		},
		detial(val){
			uni.navigateTo({
				url:'../steps/procDetail?pro='+val
			})
		}
	},
	onLoad(option){
		this.service_name=(option.val)
		this.getlist(true,'pull-down')
	},
	mounted() {
	}
}
